.header {
  height: 50px;
  font-size: 22px;
  border-bottom: 0.5px solid #0e48b6;
  padding: 5px 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  width: calc(100% - 30px);
  top: 0;

  .header__title {
    margin-bottom: 5px;
  }
}
